
import React, { useState, useEffect } from 'react'
import './hint.less'
import close from './img/x.svg'
export default function Hint() {
  const [show, setShow] = useState(true)
  const closeHint = ()=>{
    sessionStorage.setItem('hint',false)
    setShow(false)
  }
  useEffect(()=>{
    let sess = sessionStorage.getItem('hint')
    if(sess !== null){
      if(sess){
        setShow(false)
      }
    }
  },[])
  return (
    <div>
      {show && <div className='trade-hint'>
        <div className='left-hint'>
          <div>
            <svg className="MuiSvgIcon-root MuiSvgIcon-fontSizeInherit" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M12 5.99L19.53 19H4.47L12 5.99M12 2L1 21h22L12 2zm1 14h-2v2h2v-2zm0-6h-2v4h2v-4z"></path></svg>
          </div>
          <div>You are visiting the testnet website. To return to mainnet, go to  <a target="_blank" rel='noreferrer' href='https://app.deri.finance'>https://app.deri.finance/</a> 
          </div>
        </div>
        <div className='close-hint' onClick={closeHint}>
          <img src={close} alt='Close' />
        </div>
      </div>}

    </div>

  )
}